.menuBar {
  width:95%;
    border-radius: 25px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: inline-flex;
    justify-content: space-around;
    background-color: rgba(0, 0, 0, 0.4);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    align-items: center;
    padding: 0 10px;
    margin: 15px 0 0 0;
  }
 
  .link {
    list-style: none;
    color: white;
    font-family: sans-serif;
    font-weight: bold;
    padding: 12px 16px;
    margin: 0 58px;
    position: relative;
    cursor: pointer;
    white-space: nowrap;
    text-decoration: none
  }
  .link::before {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
    transition: 0.2s;
    border-radius: 25px;
  }
  .link:hover {
    color: black;
    text-decoration: none
  }
  .link:any-link:hover::before {
    background: linear-gradient(to bottom, #e8edec, #d2d1d3);
    box-shadow: 0px 3px 20px 0px black;
    transform: scale(1.2);
  }
  .menuBar svg{
      color: #fff;
      font-size: 30px;
      height: 100%;
      width: 50px;
      margin: 3px 10px 3px 10px;
  }